<script>
	// @ts-nocheck

	import { onMount } from 'svelte';

	let ready;
	let navOpen;

	// cache user's preferred wallet URL to their browser
	onMount(async () => {
		ready = true;
	});

	function handleNav() {
		navOpen = !navOpen;
	}

	function onClickOutside(event) {
		navOpen = false;
	}
	function hideNav() {
		navOpen = false;
	}
</script>

<!-- Use Menu Icon to open the sidenav -->
<div
	class="flex absolute top-0 left-0 z-40 cursor-pointer
m-6 opacity-90 w-auto"
	class:change={navOpen}
	on:keypress={handleNav}
	on:click={handleNav}
>
	<div class="inline-block">
		<div class="bar1" />
		<div class="bar2" />
		<div class="bar3" />
	</div>
</div>

{#if ready}
	<div
		class="sidenav absolute top-0 left-0 h-[15%] w-0 z-30 bg-neutral-600 overflow-x-hidden
		pt-16 transition duration-500"
		class:open={navOpen}
	>
		<slot {hideNav} />
	</div>
{/if}

<div class:mask={navOpen} on:keypress={onClickOutside} on:click={onClickOutside} />

<style>.absolute {
		position: absolute;
}.top-0 {
		top: 0px;
}.left-0 {
		left: 0px;
}.z-40 {
		z-index: 40;
}.z-30 {
		z-index: 30;
}.m-6 {
		margin: 1.5rem;
}.inline-block {
		display: inline-block;
}.flex {
		display: flex;
}.h-\[15\%\] {
		height: 15%;
}.w-auto {
		width: auto;
}.w-0 {
		width: 0px;
}.cursor-pointer {
		cursor: pointer;
}.overflow-x-hidden {
		overflow-x: hidden;
}.bg-neutral-600 {
		--tw-bg-opacity: 1;
		background-color: rgb(82 82 82 / var(--tw-bg-opacity));
}.pt-16 {
		padding-top: 4rem;
}.opacity-90 {
		opacity: 0.9;
}.transition {
		transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, -webkit-backdrop-filter;
		transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter;
		transition-property: color, background-color, border-color, text-decoration-color, fill, stroke, opacity, box-shadow, transform, filter, backdrop-filter, -webkit-backdrop-filter;
		transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
		transition-duration: 150ms;
}.duration-500 {
		transition-duration: 500ms;
}.sidenav {
		transition: 0.25s;
	}.bar1,
	.bar2,
	.bar3 {
		width: 35px;
		height: 5px;
		background-color: currentColor;
		margin: 6px 0;
		transition: 0.4s;
	}.change .bar1 {
		transform: rotate(-45deg) translate(-9px, 6px);
	}.change .bar2 {
		opacity: 0;
	}.change .bar3 {
		transform: rotate(45deg) translate(-8px, -8px);
	}.open {
		width: 80%;
		height: 100%;
		overflow-x: scroll;
	}.mask {
		width: 100%;
		height: 100%;
		position: fixed;
		top: 0;
		left: 0;
		opacity: 0.5;
		background-color: #444;
		transition: 0.4s;
		z-index: 10;
	}</style>
